.blocks {
	padding-top: 0;
	padding-bottom: 0;
}
.block {
	
	// Accordion
	// ----------------------------------------------	
	&.accordion {
		padding-bottom: 3rem;
		padding-top: 2rem;
		.text {
			margin-bottom: 0.75rem;
		}
		.intro {
			margin-bottom: 1.5rem;
		}
		.accordion {
			background: $blue;
			color: $white;
			cursor: pointer;
			font-weight: bold;
			margin-bottom: 0.75rem;
			i {
				margin-right: 0.5rem;				
			}
			.question {
				color: $white;
				font-family: $font-family-sans-serif;
				font-size: 1.1rem;
				display: block;
				padding: .75rem 1rem;
				text-decoration: none;
				.fa-minus {
					display: none;
				}
				&[aria-expanded="true"] {
					background: $gray-100;					
					color: $black;
					.fa-plus {
						display: none;
					}					
					.fa-minus {
						display: inline-block;
					}
				}
			}
			.answer {
				background: $gray-100;
				border-top: 2px solid $white;
				color: $black;
				font-weight: normal;
				padding: 1.5rem 2.5rem 1.5rem 2.5rem;
			}
		}			
	}
	
	// Blog
	// ----------------------------------------------	
	&.blog {
		.heading {
			margin-bottom: 1.5rem;
		}
		article {
			background: $white;
			margin-bottom: 2rem;
			padding: 1rem;
			h2 {
				font-size: 1.2rem;
				font-weight: 600;
				margin-bottom: 0.25rem;
			}
		}
		.entry-meta {
			font-style: italic;
		}
		.entry-header {
			margin-bottom: 1rem;
		}
		.featured-image {
			margin-bottom: 1rem;
		}
		.blog-wrapper {
			padding: 3rem 0;
		}
	}
	
	// Cards
	// ----------------------------------------------	
	&.cards {		
		
		position: relative;
		&:after {				
			background-position: center center;
			background-repeat: no-repeat;
			background-size: 100% 100%;
			bottom: 0;
			content: '';
			height: 100%;				
			position: absolute;				
			width: 100%;
			z-index: -1;
		}
		
		.row-cards {
			.col-12 {
				@include media-breakpoint-down(sm) {
					margin-bottom: 2rem;
				}
				&:nth-of-type(2) {
					margin-top: 3rem;
					@include media-breakpoint-down(sm) {
						margin-top: 1rem;
					}
				}
				&:nth-of-type(3) {
					margin-top: 2rem;
					@include media-breakpoint-down(sm) {
						margin-top: 1rem;
					}
				}
				&:nth-of-type(4) {
					margin-top: 5rem;
					@include media-breakpoint-down(sm) {
						margin-top: 1rem;
					}
				}
			}	
		}				
		.card-wrapper {
			background: #f1f1f1;
			font-size: 0.542rem;
			@include media-breakpoint-down(sm) {
				font-size: 1rem;
			}		
			.image {
				background: $white;
				padding: .5rem .5rem .25rem;
				@include media-breakpoint-down(sm) {
					padding: 1rem 1rem .75rem;
				}
			}
			.entry {
				padding: .5rem .5rem .75rem;
			}
			h3 {
				color: $blue;
				font-size: 0.583rem;
				font-weight: 600;
				margin-bottom: 0.25rem;
				text-transform: uppercase;
				@include media-breakpoint-down(sm) {
					font-size: 1rem;
				}
			}			
			.btn {
				border-radius: 1.5rem;
				font-size: 0.542rem;
				text-transform: uppercase;
				@include media-breakpoint-down(sm) {
					font-size: 1rem;
				}					
			}	
			.text {
				margin-bottom: 0.5rem;
			}
			.image {
				text-align: center;
				img {
					height: 100px;
					width: auto;
				}
			}
		}
	}
	
	// Carousel
	// ----------------------------------------------	
	&.carousel-block {
		color: $white;
		h3 {
			color: $blue;
			font-size: 0.75rem;
			font-weight: 600;
			margin-bottom: .25rem;
			text-transform: uppercase;
		}
		.btn {
			margin-top: 1.25rem;
		}
		.carousel-indicators {
			margin-bottom: 0;
			position: relative;
			@include media-breakpoint-down(sm) {
				margin-top: 1.5rem;
			}
			 li {
				 border-radius: 50%;
				 height: 30px;
				 margin-left: 15px;
				 margin-right: 15px;
			 }
		}
	}
	
	// Columns
	// ----------------------------------------------
	&.columns {
		text-align: center;
		.col-column {
			@include media-breakpoint-down(sm) {
				margin-bottom: 2rem;					
				&:last-of-type {
					margin-bottom: 0;
				}
			}				
			&.col-md-3 {
				.column {
					padding-left: 1rem;
					padding-right: 1rem;
				}
				.text {
					h3 {
						font-size: 1.083rem;
					}	
				}		
				.image {
					img {
						height: 100px;
					}
				}			
			}
		}
		h2 {
			margin-bottom: 2.5rem;
		}
		.column {
			box-shadow: 0 0 10px rgba( $black, 0.1 );
			height: 100%;				
			padding: 1.5rem 2rem;
		}
		.image {
			margin-bottom: 0.75rem;
			img {
				height: 127px;
				width: auto;
			}
		}
		.text {
			h3 {
				color: $primary;
				font-size: 1.333rem;
				font-weight: 600;
				@include media-breakpoint-down(sm) {
					br {
						display: none;
					}
				}
			}
		}
	}

	// Featured Story
	// ----------------------------------------------
	&.featured-story {
		background-size: cover;
		margin-top: 5rem;
		padding-bottom: 8rem;
		.text {
			background: $yellow;
			margin-top: -2rem;
			padding: 2rem;
		}
		.overlay {
			align-items: center;			
			display: flex;
			justify-content: center;
			height: 100%;
			width: 100%;
		}
	}
	
	// Promo Bar
	// ----------------------------------------------
	&.promo-bar {
		background: $gray-100;
		color: $white;
		padding: 2rem 0;
		.align-items-right {
			justify-content: flex-end;
		}
	}
	
	// Text with Media (Right)
	// ----------------------------------------------
	&.text-large-image-right {
		padding: 5rem 0;
		.text {
			padding-top: 2rem;
			@include media-breakpoint-down(sm) {
				margin-bottom: 2rem;
			}
		}
	}
	
	// Text with Media (Left)
	// ----------------------------------------------
	&.text-large-image-left {
		padding: 5rem 0;
		.text {
			padding-top: 2rem;
		}
	}

	// WYSIWYG
	// ----------------------------------------------
	&.wysiwyg {
		
	}
}